import React, { useEffect, useState } from "react";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import {
  Box,
  Button,
  Dialog,
  DialogContent,
  DialogTitle,
  Input,
  Tab,
  Tabs,
} from "@material-ui/core";
import MuiDialogActions from "@material-ui/core/DialogActions";
import Typography from "@material-ui/core/Typography";
import { Grid, TextField, TextareaAutosize } from "@material-ui/core";
import { getDayMonthYear, isMobile } from "../../../const/utils";
import { useDispatch } from "react-redux";
import { updateEmployee } from "app/redux/actions/EmployeeActions";
import SendLeaderDialog from "../../ManageEmployee/SendLeader/sendLeaderDialog";
import AddRequestDialog from "./AddRequestDialog";
import RefusalDialog from "./RefusalDialog";  
import ConfirmLetter from "./ConfirmLetter";
// import "styles/views/_Resignation.scss";
import moment from "moment";
import { POSITIONS } from "app/const/EmployeeConst";
import { TabPanel, a11yProps } from "app/component/CustomTab";
import "../../../views/_form.scss";

const DialogActions = withStyles((theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
  },
}))(MuiDialogActions);

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    display: isMobile() ? "block" : "flex",
  },
  tabs: {
    borderRight: `1px solid ${theme.palette.divider}`,
    flexShrink: "0",
    position: "fixed",
  },
}));

const ResignationLetter = ({
  t,
  open,
  handleClose,
  employee,
  isManage,
  handleDialogEmployeeClose,
  isEnd,
}) => {
  const [showDialogApproved, setShowDialogApproved] = useState(false);

  const [showDialogSubmit, setShowDialogSubmit] = useState(false);
  const [showDialogAddRequest, setShowDialogAddRequest] = useState(false);
  const [showDialogReasonRefusalDialog, setShowDialogReasonRefusalDialog] =
    useState(false);
  const [content, setContent] = useState({});
  const [line, setLine] = useState([]);
  const [isRegister, setIsRegister] = useState(true);
  console.log("employee", employee);
  console.log("isManage", isManage);
  console.log("isEnd", isEnd);

  useEffect(() => {
    setContent({
      ...employee?.data,
      reasonForEnding: ` ${
        employee?.data?.reasonForEnding
          ? employee?.data?.reasonForEnding
          : "L do xin ngh"
      }`,
    });
  }, [employee]);

  useEffect(() => {
    setLine(content?.reasonForEnding?.split("\n"));
  }, [content.reasonForEnding]);

  console.log("content", content);
  const classes = useStyles();
  const dispatch = useDispatch();

  const handleDialogSubmit = () => {
    setShowDialogSubmit(true);
  };
  const handleDialogSubmitClose = () => {
    setShowDialogSubmit(false);
  };

  const handleDialogApproved = () => {
    setShowDialogApproved(true);
    console.log("content", content);
    const updateData = { ...content, submitProfileStatus: 7 };
    dispatch(updateEmployee(content.id, updateData));
    handleClose();
  };

  const handleDialogAddRequest = () => {
    setShowDialogAddRequest(true);
  };

  const handleDialogAddRequestClose = () => {
    setShowDialogAddRequest(false);
  };

  const handleDialogReasonRefusalDialog = () => {
    setShowDialogReasonRefusalDialog(true);
  };

  const handleDialogReasonRefusalDialogClose = () => {
    setShowDialogReasonRefusalDialog(false);
  };

  const handleChangInput = (e, field) => {
    switch (field) {
      case "reasonForEnding": {
        if (!e.target.value.startsWith("L do xin ngh: ")) {
          setContent({
            ...content,
            reasonForEnding: "L do xin ngh: ",
          });
        } else {
          setContent({
            ...content,
            reasonForEnding: e.target.value,
          });
        }

        break;
      }
      default: {
        setContent({
          ...content,
          [field]: e.target.value,
        });
        break;
      }
    }
  };

  return (
    <div>
      {!isEnd ? (
        <Dialog
          onClose={handleClose}
          open={open}
          maxWidth={"lg"}
          fullWidth={true}
        >
          <DialogTitle sx={{ m: 0, p: 2 }} id="customized-dialog-title">
            <Grid container justify="space-between" alignItems="center">
              <Grid item>n xin ngh vic</Grid>
            </Grid>
          </DialogTitle>

          <DialogContent dividers>
            <Grid xs={12} className="flex">
              <Grid xs={2}>
                <Tabs
                  orientation={isMobile() ? "horizontal" : "vertical"}
                  value={0}
                  className={classes.tabs}
                >
                  <Tab label="n xin ngh vic" {...a11yProps(0)} />
                </Tabs>
              </Grid>
              <Grid xs={10}>
                <TabPanel value={0} index={0} className="tabPanel">
                  <DialogContent dividers className="wrapper-a4 mh-70">
                    <Box className="A4">
                      <Box className="A4-content text-justify">
                        <Typography className="flex-center fw bold">
                          CNG HA X HI CH NGHA VIT NAM
                        </Typography>
                        <Typography
                          fontWeight="bold"
                          className="flex-center heading-underline fw"
                        >
                          c lp - T do - Hnh phc
                        </Typography>
                        <Typography className="flex-center mt-32 fw bold">
                          N XIN NGH VIC
                        </Typography>
                        <Typography className="mt-32 fw text-center">
                          Knh gi: Ban Gim c cng ty <b>OCEANTECH</b>
                        </Typography>
                        <Typography className="fw">
                          Tn ti l: <b>{content?.name}</b>
                        </Typography>
                        <Typography className="fw">
                          Hin ti ang l{" "}
                          {
                            POSITIONS?.find(
                              (position) =>
                                position?.id === (content?.currentPosition ?? 1)
                            )?.name
                          }{" "}
                          ti cng ty OCEANTECH<b></b>
                        </Typography>
                        <Typography className="fw">
                          Ti lm n ny,  ngh Ban Gin c cho ti xin ngh
                          vic t ngy{" "}
                          {
                            moment(
                              new Date(
                                content?.endDay
                                  ? content?.endDay
                                  : content?.endDay
                              )
                            )
                              .format("DD/MM/YYYY")
                              .split("/")[0]
                          }{" "}
                          thng{" "}
                          {
                            moment(
                              new Date(
                                content?.endDay
                                  ? content?.endDay
                                  : content?.endDay
                              )
                            )
                              .format("DD/MM/YYYY")
                              .split("/")[1]
                          }{" "}
                          nm{" "}
                          {
                            moment(
                              new Date(
                                content?.endDay
                                  ? content?.endDay
                                  : content?.endDay
                              )
                            )
                              .format("DD/MM/YYYY")
                              .split("/")[2]
                          }
                          .
                          {isRegister && (
                            <>
                              <Input
                                id="icon-button-date"
                                className="mr-4 ml-4 fw"
                                style={{ width: "20px" }}
                                type="date"
                                inputProps={{
                                  min: moment().format("YYYY-MM-DD"),
                                  width: "20",
                                }}
                                name="endDay"
                                value={content?.endDay || ""}
                                onChange={(e) => handleChangInput(e, "endDay")}
                                InputLabelProps={{
                                  shrink: true,
                                }}
                              />
                              <span style={{ color: "red" }}> * </span>
                            </>
                          )}
                          <br></br>
                          {!isRegister &&
                            `Ngh v l do : ${content?.reasonForEnding}`}
                          {isRegister && (
                            <div className="relative fw">
                              <span
                                style={{
                                  position: "absolute",
                                  top: `0`,
                                  backgroundColor: "white",
                                  zIndex: "1001",
                                  transform: "translateY(-2px)",
                                }}
                              >
                                L do xin ngh:{" "}
                              </span>
                              <Input
                                className="no-padding custom-input fw"
                                name="reasonForEnding"
                                multiline
                                value={content?.reasonForEnding || ""}
                                onChange={(e) =>
                                  handleChangInput(e, "reasonForEnding")
                                }
                                style={{
                                  fontFamily: "Times New Roman",
                                  fontSize: "16px",
                                  display: "block",
                                  position: "relative",
                                  zIndex: "1000",
                                  width: "100%",
                                  outline: "unset",
                                }}
                              ></Input>
                              {line?.map((item, index) => (
                                <span
                                  style={{
                                    position: "absolute",
                                    top: `${(1 / line.length) * 100 * index}%`,
                                    right: "0",
                                    left: "0",
                                    width: "100%",
                                    height: line
                                      ? `${(1 / line.length) * 100}%`
                                      : "100%",
                                    borderBottom: "1px dashed",
                                    transform: "translateY(-2px)",
                                  }}
                                ></span>
                              ))}
                            </div>
                          )}
                        </Typography>
                        <Typography className="pb-12 fw">
                          Trong thi gian ch i s chp thun ca Ban Gim c
                          Cng ty, ti s tip tc lm vic nghim tc v tin
                          hnh bn giao cng vic cng nh ti sn cho ngi
                          qun l trc tip ca ti l ng/b{" "}
                          <b>{employee?.leaderName}</b>
                          <br />
                          Ti xin chn thnh cm n!
                        </Typography>
                        <Typography></Typography>
                        <Grid container className="mt-12 fw">
                          <Grid item xs={6}></Grid>
                          <Grid item xs={6}>
                            <ConfirmLetter
                              employee={employee}
                              day={
                                moment(
                                  new Date(
                                    content?.endDay
                                      ? content?.endDay
                                      : content?.endDay
                                  )
                                )
                                  .format("DD/MM/YYYY")
                                  .split("/")[0]
                              }
                              year={
                                moment(
                                  new Date(
                                    content?.endDay
                                      ? content?.endDay
                                      : content?.endDay
                                  )
                                )
                                  .format("DD/MM/YYYY")
                                  .split("/")[2]
                              }
                              month={
                                moment(
                                  new Date(
                                    content?.endDay
                                      ? content?.endDay
                                      : content?.endDay
                                  )
                                )
                                  .format("DD/MM/YYYY")
                                  .split("/")[1]
                              }
                            />
                          </Grid>
                        </Grid>
                      </Box>
                    </Box>
                  </DialogContent>
                </TabPanel>
              </Grid>
            </Grid>
          </DialogContent>
          <DialogActions>
            <div className="text-center m-auto ">
              {isManage ? (
                <>
                  <Button
                    variant="contained"
                    color="primary"
                    className="mr-12"
                    onClick={() => handleDialogApproved()}
                  >
                    Ph duyt
                  </Button>
                  <Button
                    variant="contained"
                    color="primary"
                    className="mr-12"
                    onClick={() => handleDialogAddRequest()}
                  >
                    Yu cu b sung
                  </Button>
                  <Button
                    variant="contained"
                    color="primary"
                    className="mr-12"
                    onClick={() => handleDialogReasonRefusalDialog()}
                  >
                    T chi
                  </Button>
                </>
              ) : (
                <Button
                  variant="contained"
                  color="primary"
                  type="button"
                  className="mr-12"
                  onClick={() => handleDialogSubmit()}
                >
                  Gi lnh o
                </Button>
              )}

              <Button
                variant="contained"
                color="secondary"
                type="button"
                onClick={handleClose}
              >
                Hy
              </Button>
            </div>
          </DialogActions>
        </Dialog>
      ) : (
        <div>
          <DialogContent dividers className="wrapper-a4 mh-70">
            <Box className="A4">
              <Box className="A4-content text-justify">
                <Typography className="flex-center fw bold">
                  CNG HA X HI CH NGHA VIT NAM
                </Typography>
                <Typography
                  fontWeight="bold"
                  className="flex-center heading-underline fw"
                >
                  c lp - T do - Hnh phc
                </Typography>
                <Typography className="flex-center mt-32 fw bold">
                  N XIN NGH VIC
                </Typography>
                <Typography className="mt-32 fw text-center">
                  Knh gi: Ban Gim c cng ty <b>OCEANTECH</b>
                </Typography>
                <Typography className="fw">
                  Tn ti l: <b>{content?.name}</b>
                </Typography>
                <Typography className="fw">
                  Hin ti ang l{" "}
                  {
                    POSITIONS?.find(
                      (position) =>
                        position?.id === (content?.currentPosition ?? 1)
                    )?.name
                  }{" "}
                  ti cng ty OCEANTECH<b></b>
                </Typography>
                <Typography className="fw">
                  Ti lm n ny,  ngh Ban Gin c cho ti xin ngh vic t
                  ngy{" "}
                  {
                    moment(
                      new Date(
                        content?.endDay ? content?.endDay : content?.endDay
                      )
                    )
                      .format("DD/MM/YYYY")
                      .split("/")[0]
                  }{" "}
                  thng{" "}
                  {
                    moment(
                      new Date(
                        content?.endDay ? content?.endDay : content?.endDay
                      )
                    )
                      .format("DD/MM/YYYY")
                      .split("/")[1]
                  }{" "}
                  nm{" "}
                  {
                    moment(
                      new Date(
                        content?.endDay ? content?.endDay : content?.endDay
                      )
                    )
                      .format("DD/MM/YYYY")
                      .split("/")[2]
                  }
                  .
                  {isRegister && (
                    <>
                      <Input
                        id="icon-button-date"
                        className="mr-4 ml-4 fw"
                        style={{ width: "20px" }}
                        type="date"
                        inputProps={{
                          min: moment().format("YYYY-MM-DD"),
                          width: "20",
                        }}
                        name="endDay"
                        value={content?.endDay || ""}
                        onChange={(e) => handleChangInput(e, "endDay")}
                        InputLabelProps={{
                          shrink: true,
                        }}
                      />
                      <span style={{ color: "red" }}> * </span>
                    </>
                  )}
                  <br></br>
                  {!isRegister && `Ngh v l do : ${content?.reasonForEnding}`}
                  {isRegister && (
                    <div className="relative fw">
                      <span
                        style={{
                          position: "absolute",
                          top: `0`,
                          backgroundColor: "white",
                          zIndex: "1001",
                          transform: "translateY(-2px)",
                        }}
                      >
                        L do xin ngh:{" "}
                      </span>
                      <Input
                        className="no-padding custom-input fw"
                        name="reasonForEnding"
                        multiline
                        value={content?.reasonForEnding || ""}
                        onChange={(e) => handleChangInput(e, "reasonForEnding")}
                        style={{
                          fontFamily: "Times New Roman",
                          fontSize: "16px",
                          display: "block",
                          position: "relative",
                          zIndex: "1000",
                          width: "100%",
                          outline: "unset",
                        }}
                      ></Input>
                      {line?.map((item, index) => (
                        <span
                          style={{
                            position: "absolute",
                            top: `${(1 / line.length) * 100 * index}%`,
                            right: "0",
                            left: "0",
                            width: "100%",
                            height: line
                              ? `${(1 / line.length) * 100}%`
                              : "100%",
                            borderBottom: "1px dashed",
                            transform: "translateY(-2px)",
                          }}
                        ></span>
                      ))}
                    </div>
                  )}
                </Typography>
                <Typography className="pb-12 fw">
                  Trong thi gian ch i s chp thun ca Ban Gim c Cng
                  ty, ti s tip tc lm vic nghim tc v tin hnh bn giao
                  cng vic cng nh ti sn cho ngi qun l trc tip ca ti
                  l ng/b <b>{employee?.leaderName}</b>
                  <br />
                  Ti xin chn thnh cm n!
                </Typography>
                <Typography></Typography>
                <Grid container className="mt-12 fw">
                  <Grid item xs={6}></Grid>
                  <Grid item xs={6}>
                    <ConfirmLetter
                      employee={employee}
                      day={
                        moment(
                          new Date(
                            content?.endDay ? content?.endDay : content?.endDay
                          )
                        )
                          .format("DD/MM/YYYY")
                          .split("/")[0]
                      }
                      year={
                        moment(
                          new Date(
                            content?.endDay ? content?.endDay : content?.endDay
                          )
                        )
                          .format("DD/MM/YYYY")
                          .split("/")[2]
                      }
                      month={
                        moment(
                          new Date(
                            content?.endDay ? content?.endDay : content?.endDay
                          )
                        )
                          .format("DD/MM/YYYY")
                          .split("/")[1]
                      }
                    />
                  </Grid>
                </Grid>
              </Box>
            </Box>
          </DialogContent>

          {!isEnd && (
            <DialogActions>
              <div className="text-center m-auto ">
                {isManage ? (
                  <>
                    <Button
                      variant="contained"
                      color="primary"
                      className="mr-12"
                      onClick={() => handleDialogApproved()}
                    >
                      Ph duyt
                    </Button>
                    <Button
                      variant="contained"
                      color="primary"
                      className="mr-12"
                      onClick={() => handleDialogAddRequest()}
                    >
                      Yu cu b sung
                    </Button>
                    <Button
                      variant="contained"
                      color="primary"
                      className="mr-12"
                      onClick={() => handleDialogReasonRefusalDialog()}
                    >
                      T chi
                    </Button>
                  </>
                ) : (
                  <Button
                    variant="contained"
                    color="primary"
                    type="button"
                    className="mr-12"
                    onClick={() => handleDialogSubmit()}
                  >
                    Gi lnh o
                  </Button>
                )}

                <Button
                  variant="contained"
                  color="secondary"
                  type="button"
                  onClick={handleClose}
                >
                  Hy
                </Button>
              </div>
            </DialogActions>
          )}
        </div>
      )}

      {showDialogSubmit && (
        <SendLeaderDialog
          t={t}
          open={showDialogSubmit}
          handleClose={handleDialogSubmitClose}
          employee={employee}
          content={content}
          handleDialogEmployeeClose={handleDialogEmployeeClose}
          isEnd={isEnd}
        />
      )}
      {/* {showDialogAddRequest && (
        <AddRequestDialog
          t={t}
          open={showDialogAddRequest}
          handleClose={handleDialogAddRequestClose}
          handleCloseProfile={handleClose}
          employee={employee}
          isEnd={true}
        />
      )}

      {showDialogReasonRefusalDialog && (
        <ReasonRefusalDialog
          t={t}
          open={showDialogReasonRefusalDialog}
          handleClose={handleDialogReasonRefusalDialogClose}
          handleCloseProfile={handleClose}
          employee={employee}
          isEnd={true}
        />
      )} */}
    </div>
  );
};

export default ResignationLetter;
