import React, { useEffect } from 'react'
import {
  Avatar,
  Grid,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
} from "@material-ui/core";
import ConstantList from "app/appConfig";
import Paper from "@material-ui/core/Paper";
import { GENDER, RELATIONSHIP } from "app/const/EmployeeConst";
import { formatDate } from "../../../const/utils";
import moment from "moment";
import "../../../../app/_ProfileTab.scss";



function ProfileTab(props) {
  const { employeeData, employeeFamily } = props;
  const employee = employeeData?.data || {};
  const listFamily = employeeFamily|| [];
  useEffect(()=>{
    console.log("employeeData",employeeData)
    console.log("employeeFamily",employeeFamily)
  },[])
  return (
    <>
      <div className="resume-wrapper">
        <Grid container spacing={2} justify="space-between">
          <Grid item md={4}>
            <div className="resume-image">
              <img
                alt="avatar"
                src={employee?.image || "/assets/images/avatar.jpg"}
              />
            </div>
          </Grid>
          <Grid item md={8}>
            <h3 className="resume-sub-heading">
              Cng ha x hi ch ngha vit nam
            </h3>
            <h4 className="resume-sub-heading heading-underline">
              c lp - T do - Hnh phc
            </h4>
            <h3 className="resume-heading">S yu l lch</h3>
            <h4 className="resume-sub-heading">T thut</h4>
          </Grid>
        </Grid>
        <div className="resume-body">
          <div className="resume-information">
            <h4 className="information-heading">I.Thng tin bn thn</h4>
            <div className="information-details">
              <Grid container spacing={2}>
                <Grid item md={8} lg={8} sm={8} className="flex">
                  <span className="detail-tittle">
                    1. H v tn(ch in hoa):
                  </span>
                  <span className="detail-content uppercase underline-dashed">
                    {employee?.name}
                  </span>
                </Grid>
                <Grid item md={4} lg={4} sm={4} className="flex">
                  <span className="detail-tittle">Nam/N:</span>
                  <span className="detail-content underline-dashed">
                    Gii Tnh
                    {GENDER[employee?.gender]?.name}
                  </span>
                </Grid>
              </Grid>
              <Grid container spacing={2}>
                <Grid item md={12} lg={12} sm={12} className="flex">
                  <span className="detail-tittle">2. Ngy sinh:</span>
                  <span className="detail-content underline-dashed">
                    {moment(new Date(employee?.dateOfBirth)).format(
                      "DD/MM/YYYY"
                    )}
                  </span>
                </Grid>
              </Grid>
              <Grid container spacing={2}>
                <Grid item md={12} lg={12} sm={12} className="flex">
                  <span className="detail-tittle">
                    3. Ni ng k h khu thng tr:
                  </span>
                  <span className="detail-content underline-dashed">
                    {employee?.address}
                  </span>
                </Grid>
              </Grid>
              <Grid container spacing={2}>
                <Grid item md={12} lg={12} sm={12} className="flex">
                  <span className="detail-tittle">4. in thoi lin h:</span>
                  <span className="detail-content underline-dashed">
                    {employee?.phone}
                  </span>
                </Grid>
              </Grid>
              <Grid container spacing={2}>
                <Grid item md={6} lg={6} sm={6} className="flex">
                  <span className="detail-tittle">5. Dn tc:</span>
                  <span className="detail-content underline-dashed">
                    {employee?.ethnic}
                  </span>
                </Grid>
                <Grid item md={6} lg={6} sm={6} className="flex">
                  <span className="detail-tittle">Tn gio:</span>
                  <span className="detail-content underline-dashed">
                    {employee?.religion}
                  </span>
                </Grid>
              </Grid>
              <Grid container spacing={2}>
                <Grid item md={6} lg={6} sm={6} className="flex">
                  <span className="detail-tittle">6. S cmnd/cccd:</span>
                  <span className="detail-content underline-dashed">
                    {employee?.citizenIdentificationNumber}
                  </span>
                </Grid>
                <Grid item md={3} lg={3} sm={3} className="flex">
                  <span className="detail-tittle">Ngy cp:</span>
                  <span className="detail-content underline-dashed">
                    {moment(new Date(employee?.dateOfIssuanceCard)).format(
                      "DD/MM/YYYY"
                    )}
                  </span>
                </Grid>
                <Grid item md={3} lg={3} sm={3} className="flex">
                  <span className="detail-tittle">Ni cp :</span>
                  <span className="detail-content underline-dashed">
                    {employee?.placeOfIssueCard}
                  </span>
                </Grid>
              </Grid>
            </div>
          </div>
          <div className="resume-family">
            <h4 className="family-heading">II. Quan h gia nh</h4>
            <div className="family-detail-table">
              <TableContainer>
                <Table border="1">
                  <TableHead>
                    <TableRow>
                      <TableCell
                        align="center"
                        width="6%"
                        className="table-head"
                      >
                        STT
                      </TableCell>
                      <TableCell
                        align="center"
                        width="22%"
                        className="table-head"
                      >
                        H v tn
                      </TableCell>
                      <TableCell
                        align="center"
                        width="15%"
                        className="table-head"
                      >
                        Ngy sinh
                      </TableCell>
                      <TableCell
                        align="center"
                        width="15%"
                        className="table-head"
                      >
                        Quan h
                      </TableCell>
                      <TableCell
                        align="center"
                        width="15%"
                        className="table-head"
                      >
                        S in thoi
                      </TableCell>
                      <TableCell
                        align="center"
                        width="15%"
                        className="table-head"
                      >
                        S cmnd/cccd
                      </TableCell>
                      <TableCell
                        align="center"
                        width="18%"
                        className="table-head"
                      >
                        a ch
                      </TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {listFamily?.map((familiy, index) => (
                      <TableRow key={familiy?.id}>
                        <TableCell align="center" width={50}>
                          {index + 1}
                        </TableCell>
                        <TableCell align="center">{familiy?.name}</TableCell>
                        <TableCell align="center" width={100}>
                          {formatDate(familiy?.dateOfBirth)}
                        </TableCell>
                        <TableCell align="center">
                          {
                            RELATIONSHIP.find(
                              (item) => item.value === familiy?.relationShip
                            ).name
                          }
                        </TableCell>
                        <TableCell align="center">
                          {familiy?.citizenIdentificationNumber}
                        </TableCell>
                        <TableCell align="center">
                          {familiy?.phoneNumber}
                        </TableCell>
                        <TableCell align="center">{familiy?.address}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
            </div>
          </div>
          <div className="resume-reassurance">
            <p className="mt-30 text-indent">
              Ti xin cam oan bn khai s yu l lch trn l ng s tht, nu
              c iu g khng ng ti chu trch nhim trc php lut v li
              khai ca mnh.
            </p>
          </div>
        </div>
        <div className="resume-footer">
          <div className="footer-container">
            <div className="resume-date">
              <span className="reassurance-place">Tp.H Ni</span>,
              <span className="reassurance-day">
                <span className="date-tittle">ngy</span>
                <span className="date-content underline-dashed">
                  {
                    moment(new Date(employee?.submitDay))
                      .format("DD/MM/YYYY")
                      .split("/")[0]
                  }
                </span>
              </span>
              <span className="reassurance-month">
                <span className="date-tittle">thng</span>
                <span className="date-content underline-dashed">
                  {
                    moment(new Date(employee?.submitDay))
                      .format("DD/MM/YYYY")
                      .split("/")[1]
                  }
                </span>
              </span>
              <span className="reassurance-year">
                <span className="date-tittle">nm</span>
                <span className="date-content underline-dashed">
                  {
                    moment(new Date(employee?.submitDay))
                      .format("DD/MM/YYYY")
                      .split("/")[2]
                  }
                </span>
              </span>
            </div>
            <h4 className="footer-label">Ngi khai</h4>
            <span className="footer-sub-label">(k v ghi r h tn)</span>
            <div className="footer-signature">{employee?.name}</div>
          </div>
        </div>
      </div>
    </>
  );

}

export default ProfileTab